@import "../../../css-config/mixins.scss";
.hero-banner {
  padding: 40px 0;
  position: relative;
  // This is background gradient colour
  background-image: linear-gradient(to right, #8e2de2, #4a00e0);
  @include md {
    // vh --> take complete view screen height
    height: calc(100vh - 80px);
  }
  .content {
    height: 100%;
    // Read about flex from angela afterwards
    display: flex;
    justify-content: flex-end;
    align-items: center;
    // column-reverse is reverse the ordering of div means headphone img comes 1st and then content
    flex-direction: column-reverse;
    max-width: calc(100% - 20px);
    // The auto keyword adjusts the width of the grid item to the max width of its content. That means that the element auto sizes the column
    margin: 0 auto;
    position: relative;
    // Above styling is for mobile now is for desktop
    @include md {
      flex-direction: row;
      max-width: 1200px;
    }
    .banner-img {
      position: relative;
      z-index: 9;
      width: 200px;
      margin-bottom: 20px;
      @include md {
        width: 500px;
        margin-right: 60px;
        margin-top: 50px;
        margin-bottom: 0;
      }
      @include xxl {
        width: 600px;
        margin-right: 0;
        margin-top: 0;
      }
    }
    .text-content {
      color: white;
      text-align: center;
      display: flex;
      flex-direction: column;
      align-items: center;
      @include md {
        position: absolute;
        left: 50px;
        top: 50%;
        transform: translateY(-50%);
      }
      @include xxl {
        left: 0;
      }
      h1 {
        font-size: 80px;
        font-weight: 700;
        line-height: 1;
        color: white;
        margin-bottom: 20px;
        @include md {
          font-size: 180px;
        }
      }
      p {
        max-width: 300px;
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 20px;
        @include md {
          max-width: 500px;
          font-size: 18px;
          line-height: 24px;
          margin-bottom: 40px;
        }
      }
      //   CSS for buttons
      .ctas {
        display: flex;
        justify-content: center;
        gap: 20px;
        .banner-cta {
          text-transform: uppercase;
          font-size: 13px;
          font-weight: 500;
          width: fit-content;
          border: 2px solid white;
          padding: 10px 20px;
          transition: all ease 0.3s;
          cursor: pointer;
          //   nested styling in css
          &.v2 {
            background-color: white;
            color: black;
          }
          &:hover {
            opacity: 0.6;
          }
        }
      }
    }
  }
}
