@import "../../css-config/mixins.scss";
.category-main-content {
  // adding css for mobile
  margin: 30px 0;
  @include md {
    // adding css for desktop
    margin: 75px 0;
  }
  .layout {
    // calc width for mobile
    max-width: calc(100% - 20px);
    margin: 0 auto;
    @include md {
      max-width: 1200px;
    }
  }
  .category-title {
    font-size: 24px;
    @include md {
      font-size: 34px;
    }
  }
  .products-container {
    margin: 20px 0;
    @include md {
      margin: 50px 0;
    }
  }
}
